.searchpage-logo{
    object-fit:contain;
    height:100px;
    margin-right: 50px;
    margin-top: -30px;
}
.searchpage-header{
    display: flex;
    position: sticky;
    top:0;
    background-color: white;
    z-index: 100;
    align-items: flex-start;
    padding:20px 30px 0 30px;
    border-bottom: 1px solid lightgray;
    width:100%;
 box-shadow: 5px 7px 15px rgba(0,0,0,0.35);

}

.searchpage-headerBody .searchbox .search-input{
     margin-top: 0;
     width: unset;
     margin: unset;
     max-width: unset;
}
.searchpage-options{
    display:flex;
    align-items: center;
    color:gray;
}
.searchOptionLeft , .search-option-right{
    display:flex;
}
.searchOptionLeft a, .search-option-right a{
    text-decoration: none;
    color:gray;
    margin-left: 5px;
}
.search-option-right{
    margin-left: 80px;
}
.search-option{
    display:flex;
    align-items: center;
    margin-right:20px;
    padding:5px 7px;

}
.search-option:hover{
    border-bottom: 2px solid rgba(0, 0, 200, 0.8);
}
.searchpage-result{
    margin:40px 0;
}
.searchpage-resultCount{
    color:#70757a;
    font-size: 14px;
}
.searchpage-results{
    max-width: 650px;
    margin-top: 28px;
    margin-left: 240px;
    margin-bottom:100px ;
}
.searchpage-result-title{
    text-decoration: none;
}
.searchpage-result-title:hover{
    text-decoration: underline;
}
.searchpage-result-title h2{
    font-weight: 500;
}
.searchpage-result-snippet{
    margin-top: 10px;
}
.searchpage-result-image{
    object-fit: contain;
    height:20px ;
    width:20px;
    margin-right: 10px;
}