.search-input{
    display:flex;
    align-items: center;
    border:1px solid lightgray;
    border-radius: 999px;
    height:50px;
    padding:10px 20px;
    width:75vw;
 max-width: 560px;
 margin: 0 auto;
 
}
.search-input input{
    flex:1;
    padding:10px 20px;
    border:none;
    outline:none;
    font-size: 20px;
    font-weight: 100;
    background-color: transparent;
}
.search-icon{color:gray;cursor:pointer;}
.microphone{
    color:blue;
    cursor:pointer;
}
.search-buttons{
    margin-top:30px;
    display: flex;
    justify-content: center;
}
.search-buttons button{
    margin:5px;
    padding:7px 15px;
    background-color: #f8f8f8;
    border:1px solid white;
    text-transform: inherit;
    color:#5f6368;
}
.search-buttons button:hover{
    box-shadow: 0 1px 1px rgba(0,0,0,0.1);
    background-image: -webkit-repeating-linear-gradient(top , #f8f8f8 , #f1f1f1);
    background-color: #f8f8f8;
    border:1px solid #c6c6c6;
    columns: #222222;
}
.hide-me{
    display:none  !important;
}