.home{
    display: flex;
    height:100vh;
    flex-direction: column;
}


.home-header{
    display:flex;
    justify-content: space-between;
    padding:20px 30px;
    align-items: center;
}
.home-header a{
    margin-right: 20px;
    text-decoration: inherit;
    color:rgba(0,0,0,0.87);
    font-size: 15px;
}
.header-right{
    display: flex;
   align-items: center;
   min-width: 13vw;
   justify-content: space-between;
}
.header-right > .MuiSvgIcon-root{
    margin-right: 20px;
}
.home-header a:hover{
    text-decoration: underline;
}
.home-body{
    flex:1;
    display:flex;
    /* margin-top:10%; */
    flex-direction: column;

}
.home-body > img {
    object-fit: contain;
   /* height:100px; */
}